 * 6. Remove default gradient in Android Firefox / FirefoxOS
 */
button,
input,
select,
textarea {
  margin: 0;
  /* 3 */
  font-size: 100%;
  /* 2 */
  font-family: inherit;
  /* 1 */
  outline-offset: 0;
  /* 4 */
  outline-style: none;
  /* 4 */
  outline-width: 0;
  /* 4 */
  -webkit-font-smoothing: inherit;
  /* 5 */
  background-image: none;
  /* 6 */
  text-indent: 0;
  padding-left: 0;
  padding-right: 0;
}
