.wrap-page {
  position: relative;
  display: block;
  width: 1000px;
  margin: 0 auto;
  background: #fff;
  padding: 30px 20px;
  overflow: hidden;
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.bg-top {
  position: absolute;
  top: 0;
  left: 0;
}
.bg-top:before {
    content: '';
    position: absolute;
    left: 100px;
    top: -120px;
    width: 173px;
    height: 173px;
    border-radius: 50%;
    background: #5980ff;
    opacity: 0.3;
}
.bg-top:after {
    content: '';
    position: absolute;
    top: -142px;
    left: -80px;
    width: 276px;
    height: 276px;
    border-radius: 50%;
    background: #5980ff;
    opacity: 0.7;
}
.bg-bottom {
  position: absolute;
  bottom: 0;
  right: 0;
}
.bg-bottom:before {
    content: '';
    position: absolute;
    bottom: -40px;
    right: 100px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: #5980ff;
    opacity: 0.3;
}
.bg-bottom:after {
    content: '';
    position: absolute;
    right: -70px;
    bottom: -110px;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: #5980ff;
    opacity: 0.7;
